
@mixin pad-larger{
    @media (max-width:920px){
        @content
    }
}

@mixin pad{
    @media (max-width:768px){
        @content
    }
}

@mixin pad-sm{
    @media (max-width:600px){
        @content
    }
}


@mixin mobile{
    @media (max-width:376px){
        @content
    }
}
